<template>
  <v-app>
    <v-snackbar
      v-model="snackbar"
      multi-line
      color="error"
      >
      {{ errorMessage }}
    </v-snackbar>
    <v-main>
      <v-container
        class="fill-height"
        fluid
      >
        <v-row
          align="center"
          justify="center"
        >
          <v-col
            cols="12"
            sm="8"
            md="4"
          >
            <v-card class="elevation-12 pa-12 justify-center">
              <v-row>
                <v-img
                  contain
                  height="150"
                  width="150"
                  :src="require('@/assets/KiwriousLogo_500x500.png')"
                ></v-img>
              </v-row>
              <v-card-text>
                <v-form lazy-validation ref="form" v-model="valid">
                  <v-text-field
                    v-model="username"
                    label="Username or Email"
                    name="login"
                    prepend-icon="mdi-account"
                    type="text"
                    :rules="nameRules"
                    required
                  ></v-text-field>
                  <v-text-field
                    v-model="password"
                    id="password"
                    label="Password"
                    name="password"
                    prepend-icon="mdi-lock"
                    type="password"
                    :rules="passwordRules"
                    required
                    v-on:keyup.enter="signIn()"
                  ></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-btn 
                    block large 
                    color="primary" 
                    @click="signIn()"
                    :loading="isLoading"
                    :disabled="!valid"
                  >Login</v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
        
      </v-container>

    </v-main>
   

  </v-app>
  
</template>

<script>
  import axios from 'axios'
  import qs from 'qs'
  const api = axios.create({
    baseURL: process.env.VUE_APP_URL
  })
  const config = {headers: {'Content-Type': 'application/x-www-form-urlencoded'}}
  export default {
    props:['id'],
    data() {
      return {
        valid: true,
        username: '',
        password: '',
        confirmation: '',
        name:'',
        email:'',
        organization:'',
        password_message: '',
        isLoading: false,
        formMode: 1,
        nameRules: [
          v => !!v || 'Name is required',
        ],
        passwordRules: [
          v => !!v || 'Password is required',
        ],
        snackbar: false,
        errorMessage: '',
      }
    },
    methods: {
      signIn() {
        this.$refs.form.validate();
        if (!this.username || !this.password) {
          return;
        } 
        var userInfo = {loginId: this.username}
        const url = '/auth/SignIn';
        this.isLoading = true;
        api.post(url, qs.stringify(userInfo),config)
        .then((response) => {
          console.log(response);
          if (response.status === 200) {
            if(!response.data){
              this.isLoading = false;
              this.snackbar = true;
              this.errorMessage = 'Please check your username or email';
              return;
            }
            if (this.password == response.data.Password) {
              this.$store.dispatch('setRoleData', response.data.Role)
              this.$store.dispatch('setUidData', response.data.Id)
              this.$store.dispatch('setOidData', response.data.OrganizationId)
              this.$store.dispatch('setNameData', response.data.Name)
              this.$store.dispatch('setAchivementsData', response.data.achivements)
              if(this.id){
                this.$router.push({path: '/finding/'+this.id});
              } else {
                this.$router.push({path: '/home/myinquiries'})
              }
            } else {
              this.isLoading = false;
              this.snackbar = true;
              this.errorMessage = 'Wrong password'
            }
          }
          console.log(response)
        })
        .catch((error) => {
          console.log(error);
          this.isLoading = false;
          if (error.message == 'Request failed with status code 404') {
            const e = 'Something went wrong';
            this.snackbar = true;
            this.errorMessage = e;
          }
          console.log(error.message)
        })     
      },
      register(){
        if (this.password != this.confirmation) {
          this.$message({message: "Password confirmation didn't match",type: 'error'});return;
        } 
        if(!this.username){this.$message({message:"Username cannot be empty", type: 'error'});return;}
        if(!this.email){this.$message({message:"Email cannot be empty", type: 'error'});return;}
        if(!this.name){this.$message({message:"Name cannot be empty", type: 'error'});return;}
        if(!this.organization){this.$message({message:"Name cannot be empty", type: 'error'});return;}
        if(!this.password){this.$message({message:"Password cannot be empty", type: 'error'});return;}
        var newUser = {name: this.name, roleName: 'Student', email: this.email, password: this.password, nickname: this.username}
        const url = '/user/';
        this.isLoading = true;
        api.post(url, qs.stringify(newUser),config).then((response)=>{
          this.isLoading = false;
          this.$message({message: "Registration completed successfully",type: 'success'});
          var un = this.username;
          this.changeForm(1);
          this.username = un;
        }).catch((error)=>{
          this.isLoading = false;
          this.$message({
            message: error.message,
            type: 'error'
          });
        });
      },
      changeForm(mode){
        this.username = '';
        this.password = '';
        this.confirmation = '';
        this.organization = '';
        this.name = '';
        this.email = '';
        this.formMode = mode;
      }
    },
    watch: {
      // username(){
      //   this.$refs.form.validate();
      // },
      // password(){
      //   this.$refs.form.validate();
      // }
    }
  }
</script>

<style lang="scss">

  .login-button{
    margin-top: 20px !important;
    width: 70% !important;
  }
  #login_div {
    margin-top: 180px;
  }
  #login_card {
    text-align: center;
    p {
      margin: 0; 
    }
    img {
      height: 140px;
    }
    .el-input {
      font-size: 18pt;
      margin-bottom: 10px;
    }
    .el-input__inner {
      color: gray;
      width: 70%;
      background-color: white !important;
      border-width: 0px 0px 1px 0px;
      text-align: center;
    }
    // .el-button {
    //   margin-top: 20px;
    //   width: 50%;
    //   background-color: #178de7;
    //   color: #fff;
    // }
    // .el-button:hover{
    //   background-color: #1372ba;
    // }
    .error {
      color: red;
    }
    .switch-button{
      cursor: pointer;
      margin-top: 20px;
      color: gray;
      transition: color 0.5s;
    }
    .switch-button:hover{
      color: black;
    }
  }
</style>