<template>

  <v-app>

    <v-navigation-drawer
      permanent
      :clipped="$vuetify.breakpoint.lgAndUp"
      app
      width=220
    >
      <v-list nav>
        <v-list-item
          v-for="item in items"
          :key="item.text"
          link
          router
          :to="item.index"
          two-line
          class="list-item"
        >
          <v-list-item-avatar>
            <v-img :src="getImg(item.thumbnail)" height="35" width="35"></v-img>
          </v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title class="font-weight-bold text-left" style="font-size: 22px;">{{ item.text }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

      </v-list>
    </v-navigation-drawer>

    <v-app-bar
      :clipped-left="$vuetify.breakpoint.lgAndUp"
      app
      dense
    >
      <v-toolbar-title>
        <v-img :src="require('@/assets/kiwriouslogo@2x.png')" height="35" width="35" ></v-img>
      </v-toolbar-title>
       <v-toolbar-title class="font-weight-bold" style="margin-left: 10px; font-size: 22px;">
        Kiwrious
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-chip
        class="ma-2"
        color="#FFFFFF"
        text-color="black"
      >
        <v-avatar left style="margin-right: 5px;">
          <v-icon>mdi-account-circle</v-icon>
        </v-avatar>
        <div style="font-size: 20px;">
        {{userName}}
        </div>
      </v-chip>
    </v-app-bar>

    <v-main>
      <v-container>
        <router-view />
      </v-container>
    </v-main>

  </v-app>

</template>

<script>

export default {
  props: {
    source: String,
  },
  name: 'Home',
  data: () => ({
    userName:'',
    items: [
      { icon: 'mdi-contacts', text: 'Discover', index:'/home/discover', thumbnail: "search-24-px@2x" },
      { icon: 'mdi-history', text: 'My Inquiries', index:'/home/myinquiries',thumbnail: "whatshot-24-px@2x" },
      { icon: 'mdi-content-copy', text: 'Challenges', index:'/home/challenges',thumbnail: "flash-on-24-px@2x" },
      { icon: 'mdi-cog', text: 'Tutorials', index:'/home/learn' ,thumbnail: "help-24-px@2x"},
    ],
    getImg(thumbnail){
      return require('@/assets/'+thumbnail+'.png');
    }
  }),
  created(){
    this.userName = sessionStorage.name;
  },
 
}
</script>

<style lang="scss" scoped>
  
  .list-item:hover{
    background-color: #FFECE4 !important;
  }
  .list-item:focus {
    background-color: #FFECE4 !important;
  }
</style>
